<span class="token operator">&lt;</span><span class="token operator">/</span>div<span class="token operator">-</span>component<span class="token operator">></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p>Ensuite dans la partie js il faut déclarer la variable à traiter</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">export</span> <span class="token keyword">default</span><span class="token punctuation">{</span>
